import React, { Component } from 'react';
import { samstarfsadilarInfo } from "../Assets/content.js";
import SamstarfsadilarContents from "./BirgjarComponent/SamstarfsadilarContent";
import { birgjarInfo } from "../Assets/content.js";
import BirgjarContents from "./BirgjarComponent/BirgjarContent";

class Samstarfsaðilar extends Component {
    render() {
        console.log(samstarfsadilarInfo);
        let left = true
        const samContent = samstarfsadilarInfo.map(birg => {
            left = !left;
            return (
                <SamstarfsadilarContents
                    key={birg.name}
                    name={birg.name}
                    text={birg.text}
                    slideLeft={left}
                    />
            )
        });
        const birgjarcontent = birgjarInfo.map(birg => {
            left = !left;
            return (
                <BirgjarContents
                    key={birg.text}
                    text={birg.text}
                    logo={birg.logo}
                    link={birg.link}
                    slideLeft={left}
                    />
            )
        });
        return(
            <div>
                <h1>Samstarfsaðilar</h1>
                <div>
                    {samContent}
                    {samContent}
                </div>
                <h1>Birgjar</h1>
                <div>
                    {birgjarcontent}
                    {birgjarcontent}
                </div>
            </div>
        )
        
    }
}
export default Samstarfsaðilar;
